<template>
  <div class="dialogWrapper" v-show="visible">
    <div class="dialog">
      <header>{{ title }}
        <span class="close" @click="close">X</span>
      </header>
      <main>
        <slot></slot>
      </main>
    </div>
  </div>
</template>

<script>
  export default {
    name:'myDialog',
    props:['title','visible'],
    methods: {
      close(){
        this.$emit('close')
      }
    }
  }
</script>

<style lang="scss" scoped>
  .dialogWrapper {
    position: fixed;
    background :hsla(0,0,0,0.25);
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .dialog {
    background:white;
    min-height: 12em;
    min-width: 25em;
    > header {
      padding: 16px;
      display: flex;
      justify-content: space-between;
      border-bottom: 1px solid lightgray;
    }
    > main {
      padding: 16px;
    }
    .close {
      cursor: pointer;
      font-size:20px;
    }
  }
</style>


